@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap");
html, body, #root {
  width: 100%;
  height: 100%;
  background-color: hsl(27, 66%, 92%);
  font-family: "DM Sans";
}
html .App, body .App, #root .App {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
}
html .attribution, body .attribution, #root .attribution {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 30px;
  text-align: center;
}/*# sourceMappingURL=pageStyles.css.map */